.Signupbutton {
  margin-top: 20px;
  background-image: linear-gradient(
    120deg,
    #84fab0 0%,
    #f1a1b9 50%,
    #8fd3f4 100%
  );
  width: 120px;
  height: 50px;
  border: 3px solid #4d3c3d;
  border-radius: 25px;
  font-weight: 800;
  font-size: larger;
  color: #4d3c3d;
  text-transform: uppercase;
}

.Signupbutton:hover {
  background-image: linear-gradient(
    120deg,
    #8fd3f4 0%,
    #f1a1b9 50%,
    #84fab0 100%
  );
  color: red;
}
