.App {
  min-height: 100vh;
  background-image: linear-gradient(
    120deg,
    #84fab0 0%,
    #f1a1b9 50%,
    #8fd3f4 100%
  );
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

Input {
  padding: 3px;
  margin: 10px;
  border: 1px solid #4d3c3d;
  border-radius: 5px;
  box-shadow: 1px 1px 3px #4d3c3d;
  background-color: white;
}
